.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 300px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.title {
  text-transform: uppercase;
  color: #fff;
  margin: 0;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

.search_input {
  width: 300px;
  outline: rgba(0, 0, 0, 0);
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translate(-50%, -15%);
}

.btn {
  width: 37px;
  height: 35px;
  cursor: pointer;
  background: #1b2735;
  border: none;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 16.3%;
  left: 50%;
  transform: translate(365%, -50%);
}

.days_query {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
  position: absolute;
  top: 24%;
  left: 44%;
  transform: translate(-24%, -50%);
}

.weather_text {
  color: #fff;
  font-size: 18px;
}

.days {
  gap: 10px;
  display: flex;
}

.day_value {
  width: 37px;
  height: 35px;
  color: #fff;
  cursor: pointer;
  background: #1b2735;
  border-radius: 50%;
  outline: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  display: flex;
}

ul {
  flex-wrap: wrap;
  gap: 30px;
  margin-top: 30px;
  display: flex;
  position: absolute;
  top: 40%;
}

@media screen and (min-width: 300px) {
  ul {
    flex-wrap: nowrap;
  }
}

li {
